*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  width: 90%;
  margin: 5em auto;
  max-width: 1400px;
}

main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2em;
  /* border: 1px solid magenta; */
}

@media screen and (min-width:740px)  {
 main {
   flex-direction: row;
   justify-content: space-around;
   align-items: center;
 }
}

.header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  gap: 1em;
}

.header h1 {
  font-size: 1.5rem;
  padding: 0 1em;
  font-weight: 600;
  line-height: 1.2;
}

.header p{
  font-size: .875rem;
  padding: 0 2em;
  line-height: 1.7;
  font-weight: 400;
}

@media screen and (min-width:740px) {
  .header {
    text-align: left;
    justify-content: center;
    align-items: flex-start;
    /* border: 1px solid magenta; */
    width: 50%;
  }

  .header h1 {
    padding: 0;
    font-size: 3.8rem;
  }

  .header p {
    padding: 0;
    font-size: 1rem;
  }
}

.form-section {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

@media screen and (min-width: 740px) {
  .form-section {
    width: 30%;
  }
}

.form-section-msg {
  background-color: var(--Blue);
  padding: 1em 6em;
  text-align: center;
  border-radius: 8px;
  box-shadow: var(--shadow);
}

.form-section-msg p {
  font-size: .8rem;
  font-weight: 300;
  letter-spacing: .02em;
}



@media screen and (min-width:740px) {
  .form-section-msg {
    padding: .5em;
    width: 100%;
  }
}

form {
  background-color: #fff;
  margin: 1em 0;
  border-radius: 10px;
  padding: 1em;
  box-shadow: var(--shadow);
}

form div {
  margin: 1.5em 0;
  position: relative;
}

@media screen and (min-width:740px) {
  form div {
    margin: .8em 0;
  }
}

form div:first-of-type {
  margin: .5em 0;
}

form div:last-of-type {
  margin: .5em 0;
}

form div input {
  width: 100%;
  padding: 1.5em;
  border-radius: 5px;
  border: 1px solid var(--GBlue);
  color: #bcb8b1;
  font-weight: 700;
}

form div button {
  width: 100%;
  padding: 1em;
  background-color: var(--Green);
  border: 0;
  border-radius: 5px;
  color: #fff;
  font-size: 1rem;
  text-transform: uppercase;
  font-weight: 500;
  letter-spacing: .05em;
  margin: 0;
  box-shadow: 0 4px 0 hsl(154, 65%, 40%);
}

form p {
  margin: 0;
  color: #bcb8b1;
  text-align: center;
}

form p strong {
  color: var(--Red);
}

@media screen and (min-width:740px) {
  form div input {
    padding: 1em;
  }

  form div button {
    padding: .8em;
  }

  form p {
    padding:0 2em;
    font-size: .875rem;
  }

}

footer {
  font-size: .6rem;
  letter-spacing: .03em;
  text-align: center;
}


