.error-field {
  margin-top: .5em;
  margin-bottom: 0;
  color: var(--Red);
  text-align: right;
  font-style: italic;
  font-size: .675rem;
}

.ri-error-warning-fill {
  color: var(--Red);
  position: absolute;
  top: 20%;
  left: 90%;
}

